
.form_container{
    display: flex;
    justify-content: center;
}
.form {
  width: 400px;
  display: flex;
  flex-direction: column;
  padding: 16px;
  border: 1px solid grey;
  border-radius: 10px;
  margin:30px 0;
  background-color: rgb(247, 247, 247);
}
.input {
  width: 100%;
  border: 1px solid rgba(53, 53, 53, 0.2);
  border-radius: 4px;
  cursor: pointer;
  line-height: 2;
  transition: all var(--main-transition);
  outline: none;
}
.label {
  display: block;
  align-items: center;
  margin-bottom: 8px;
  font-size: 18px;
  line-height: 1.17;
  letter-spacing: 0.01em;
  color: black;
}
.input:focus {
  border-color: #2196f3;
}
.btn{ 
    font-family:'Segoe UI', sans-serif;
    min-width: 100px;
    padding: 5px;
    margin-top: 10px;
    align-items: center;
    font-size: 20px;
    line-height: 1.87;
    font-weight: 700;
    letter-spacing: 0.06em;
    border: none;
    outline: none;
    color: #0e0d0d;
    cursor: pointer;
    background-color: rgb(203, 206, 201);
    box-shadow: 0px 2px 2px rgba(3, 3, 3, 0.15);
    border-radius: 5px;
    transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);}
    .btn:hover,
   .btn:focus {
      background-color:  #2196f3;
  }

